:root {
  scroll-behavior: smooth;
  --color-black: #242424;
  --color-white: #f4f4f4;
  --color-quote-text: rgba(244, 244, 244, .6);
  --color-border: rgba(244, 244, 244, .1);
  --color-text-card: rgba(244, 244, 244, .4);
  --color-not-active: rgba(36, 36, 36, .6);
  --color-megawhite: #fff;
  --color-footer: #c6cdd1;
  --color-pagination: #d4d4d4;
  font-family: Neue Montreal, sans-serif;
}

@font-face {
  font-family: Neue Montreal;
  src: local(Neue Montreal Medium), local(Neue-Montreal-Medium), url("NeueMontreal-Medium.0edc0724.woff2") format("woff2"), url("NeueMontreal-Medium.3c93ed65.woff") format("woff"), url("NeueMontreal-Medium.14ce3e89.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: Neue Montreal;
  src: local(Neue Montreal Italic), local(Neue-Montreal-Italic), url("NeueMontreal-Italic.52c40548.woff2") format("woff2"), url("NeueMontreal-Italic.fbf19035.woff") format("woff"), url("NeueMontreal-Italic.0fef0c2c.ttf") format("truetype");
  font-weight: normal;
  font-style: italic;
}

@font-face {
  font-family: Neue Montreal;
  src: local(Neue Montreal Regular), local(Neue-Montreal-Regular), url("NeueMontreal-Regular.7559b1a8.woff2") format("woff2"), url("NeueMontreal-Regular.f021a830.woff") format("woff"), url("NeueMontreal-Regular.e32a9fa8.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

body {
  color: var(--color-black);
  background-color: var(--color-white);
  font-family: Neue Montreal, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  font-family: inherit;
}

img {
  max-width: 100%;
  display: block;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.container {
  width: 100%;
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
  }
}

.header-content {
  justify-content: space-between;
  align-items: center;
  padding-top: 24px;
  padding-bottom: 24px;
  display: flex;
}

.burger-icon {
  stroke: var(--color-black);
}

.open-burger {
  background-color: inherit;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (max-width: 767.98px) {
  .header-nav-menu, .header-social-media {
    width: 1px;
    height: 1px;
    white-space: nowrap;
    -webkit-clip-path: inset(100%);
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    border: 0;
    margin: -1px;
    padding: 0;
    position: absolute;
    overflow: hidden;
  }
}

.header-menu-list {
  background-color: var(--color-black);
  color: var(--color-white);
  border-radius: 60px;
  align-items: center;
  column-gap: 20px;
  padding: 8px 36px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  display: flex;
}

.active-btn {
  background-color: var(--color-white);
  color: var(--color-black);
  border-radius: 30px;
  padding: 6px 12px;
}

.social-list {
  align-items: center;
  column-gap: 12px;
  display: flex;
}

.social-icon {
  fill: var(--color-black);
  opacity: .8;
  transition: all .6s ease-in-out;
}

.social-item {
  background-color: inherit;
  border: 1px solid rgba(36, 36, 36, .1);
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  padding: 4px 4px 0;
  transition: all .6s ease-in-out;
  display: flex;
}

.social-item:hover, .social-item:focus {
  background-color: var(--color-black);
  opacity: .8;
}

.social-item:focus .social-icon, .social-item:hover .social-icon {
  fill: var(--color-white);
}

