/*================================================ FOOTER ==================================================== */
.footer {
  padding-bottom: 20px;
  padding-top: 40px;
  background-color: var(--color-black);
}
.footer-logo {
  fill: var(--color-white);
}
.footer-upper {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  align-items: flex-start;
  margin-bottom: 40px;
}
.footer-title {
  color: var(--color-white);
  font-size: 40px;
  font-weight: 400;
  line-height: 1.2;
  letter-spacing: -0.02em;
  margin-bottom: 20px;
}
.footer-subscribe-text {
  color: var(--color-white);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
  margin-bottom: 20px;
}
.footer-input {
